* {
    font-family: 'IBM Plex Sans';
    /* font-family: 'Open Sans', sans-serif; */
    background-color: #f6f6f6;
}

.page__catalog {
    margin-top: 32px;
}

.catalog {
    display: grid;
    grid-template-columns: repeat(auto-fill, 324px);
    column-gap: 24px;
    row-gap: 48px;
    justify-content: center;
}

.scale {
    display: inline-block;
}

 .scale img {
    -webkit-transition: 0.5s;
    transition: 0.5s;
    display: block;
}

 .scale img:hover {
    -webkit-transform: -webkit-scale(1.05);
	transform: scale(1.05);
}

.catalog__item {
  max-width: 350px;
}


.card__description {
  padding: 12px 0px;
  box-sizing: border-box;
}

.card__title {
  margin: 0;
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
  padding: 5px;
}

.card__url {
    display: block;
    color: #3C39EC;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.5;
    text-decoration: none;
    margin-top: 8px;
    padding: 5px;
}

.card__info {
    font-size: 14px;
    color: #0C0C0D;
    margin: 8px 0 0;
    -webkit-opacity: 0.4;
    opacity: 0.4;

}

.card__info{display: inline;}

.bottom {
    position:relative;
    bottom: -3px;
    left: 0px;
}


.header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 96px;
    z-index: 9;
    position: relative;
}

.header__logo {
  margin-right: 8px;
  width: 32px;
}

.header__title {
    font-size: 20px;
    line-height: 1.6;
    margin: 0;
    text-shadow: 4px -2px 3px #11111145;
}

.header__epigraph {
    /* width: 32px; */
    text-shadow: 4px -2px 3px #11111145;
}

.page {
    margin: 0;
    box-sizing: border-box;
    padding: 0 24px;
}

.card__description a {
    word-wrap: break-word;
}


.next-left {
    float: left;
}

.next-right {
    float: right;
}




.selector,
.selector-simple {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 214px;
    overflow: hidden;
    background-size: cover !important;
    object-fit: cover;
    object-position: center;
    border: 1px solid rgba(114, 113, 113, 0.62);
    border-radius: 4px;
    -webkit-box-shadow: 5px 5px 30px rgb(0 0 0 / 50%);
    box-shadow: 5px 5px 30px rgb(0 0 0 / 50%);


    /* transition: 0.5s; */
    -webkit-transition: -webkit-transform 1.75s cubic-bezier(.075, .5, 0, 1), -webkit-box-shadow .75s ease-out;
    transition: transform 1.75s cubic-bezier(.075, .5, 0, 1), box-shadow .75s ease-out;

    /* transition: box-shadow 2.75s ease-out; */
    will-change: -webkit-transition;
    will-change: transition;
}

.selector:hover  {
    -webkit-box-shadow: 5px 5px 30px rgb(243 243 129/ 70%);
    box-shadow: 5px 5px 30px rgb(243 243 129/ 70%);
}

.card-items {
	position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 50%;
	top: 35vh;
	z-index: 1;
}

.background-grey {
	position: fixed;
	background-color:#0c0c0d5e;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 1;
}

.background-white {
	position: absolute;
	background-color: #f6f6f6;
	display: flex;
	padding: 20px;
	gap:20px;
	border-radius: 16px;
	width: auto;
	height: auto;
	z-index: 1;
}

@keyframes zoomIn {
	from {
        -webkit-opacity: 0;
        opacity: 0;
		-webkit-transform: -webkit-scale3d(.3, .3, .3);
		transform: scale3d(.3, .3, .3);
	}

	50% {
        -webkit-opacity: 1;
		opacity: 1;
	}
}

.zoomIn {
	-webkit-animation: zoomIn 1s 1;
	animation: zoomIn 1s 1;
    -webkit-opacity: 1;
	opacity: 1;

}

.card-item {
	width: 300px;
	height: auto;
}

@media  (max-width: 720px)  {
    .catalog {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        column-gap: 24px;
        row-gap: 48px;
        justify-content: center;
        justify-items: center;
    }
    .page {
        padding: 0;
    }
    .catalog__item {
        width: 100%;
    }

}

@media (max-width: 479px) {

    header {
        flex-direction: column;
    }

    .header__title {
        font-size: 1em;
        word-wrap: break-word;
    }

    .card__description h3 {
        font-size: 14px;
    }

    .card__description a {
        line-height: 1.2em;
        font-size: 12px;
    }

}

@media (max-width: 260px) {
    .catalog {
        grid-template-columns: repeat(1, 1fr);
    }
    .selector, .selector-simple {
        height: 150px;
        width: auto;
    }
}
